import { ItemsProvider } from "./_state/provider"
import { usePortal } from "Hooks"
import Item from "./item"

function Items(props) {
  return (
    <ItemsProvider {...props}>
      {usePortal(<Item>{props.children}</Item>, props.element)}
      {props.loader
        ? usePortal(
            <div className="shopwp-skeleton">{props.loader}</div>,
            props.element
          )
        : null}
    </ItemsProvider>
  )
}

export default Items
